export default (state, action) => {
  switch (action.type) {
    case 'SEND_MESSAGE_APP_SUCCESS':
      return {
        ...state,
        message: action.data
      }

      case 'SEND_MESSAGE_APP_FAILURE':
      return {
        ...state,
        message: action.errors.titleErrors[0]
      }

    default:
      return state;
  }
}