import {Observable, Subject} from 'rxjs'
import {mapObjIndexed, map, compose} from 'ramda'

const action$ = new Subject()

export const isObservable = (obj) =>  obj instanceof Observable

export const createStore = (initstate, reducers, middlewares) => action$.startWith(initstate).scan(reducers)

export const createAction = (func) => (...args) => {
  const action = func(...args)
  action$.next(action)

  return action
}

const updateState = (reducers, action) => (state, key) => reducers[key](state, action)

export const combineReducers = (reducers) => (state, action) => mapObjIndexed(updateState(reducers, action), state)

export const combineMiddleware = (middlewares) => (action) => Observable.merge(...map(middleware => middleware(action))(middlewares))
