const {
    shunt,
    matchBrackets,
    checkExpression
} = require('./shuntingyard')

const {
    calculate,
    rollDice
} = require('./roller')

const {
    map,
    chain,
    get
} = require('../utils/either')

const {
    compose
} = require('ramda')

const {
    parse,
    fold
} = require('./parser')

const {
    expression
} = require('./diceparser')

const {
    log
} = require('../utils/logging')

const calculateDice = (dicetext) => {
    const result = compose(get, map(calculate), chain(checkExpression), map(shunt), chain(matchBrackets), map(rollDice), fold, parse(expression))(dicetext)

    return result
}

module.exports = {
    calculateDice
}