import {h, render} from 'preact'
import {App} from './components/App'
import {createStore} from './store'
import {combinedReducer} from './reducers'
import {combinedMiddleware} from './middleware'

const container = document.getElementById('app')

const initState = {
    app: {
        message: ''
    },
    list: {
        cards: [
            {id: 1, text: '10d6>3 + 3', isOpen: false},
            {id: 2, text: '5d4kh2 - 2', isOpen: false},
            {id: 3, text: '2d20 + 12 + 2', isOpen: false},
            {id: 4, text: '1d20 - 3', isOpen: false},
            {id: 5, text: '2d8 + 9', isOpen: false},
            {id: 6, text: '2d20>5 + 4', isOpen: false}
        ]
    }
}

createStore(initState, combinedReducer, combinedMiddleware).subscribe(state => {
    console.log('render')
    render(<App {...state} />, container, container.lastChild)
})