import {h} from 'preact'
import {cardCalculated} from '../../actions/cards'

const cardClicked = (card) => (e) => {
    console.log(e)
     cardCalculated(card.id)
}

export const Card = ({card}) => (
    <div className='card-stack--card'>
        <div className='card-stack--card-details' onClick={cardClicked(card)}>
            <div className='card-stack--card-text'>{card.text}</div>
            <div className='card-stack--card-result'>{card.result}</div>
        </div>
    </div>
)