import {
    Observable
} from 'rxjs'

import {
    fork
} from '../utils/task'

const task = action => {
    const {
        task
    } = action

    if (task === undefined) {
        return Observable.of(action)
    }

    const {
        computation,
        computing,
        success,
        failure
    } = task

    const taskRunner = Observable.create(observer => {

        observer.next(computing())

        fork((error) => {
            observer.next(failure(error))
            observer.complete()
        }, (data) => {
            observer.next(success(data))
            observer.complete()
        }, computation)
    })

    return taskRunner
}

export default task