import {map, merge} from 'ramda'
import {calculateDice} from '../calculator/calculator'

const calculateCard = (id) => (card) => (id === card.id) ? merge(card, {result: calculateDice(card.text)}) : card

export default (state, action) => {
  switch (action.type) {
    case 'CALCULATE_CARD':
      return merge(state, {
        cards: map(calculateCard(action.id))(state.cards)
      })

    default:
      return state;
  }
}