import {Observable} from 'rxjs'
import {compose, map, toPairs, fromPairs, pick, keys, reject, isNil} from 'ramda'

const validator = action => {
  const { validate } = action

  if (validate === undefined){
    return Observable.of(action)
  }

  const { rules, data = {}, success, failure, reset } = validate

  const apply = (rules) => ([key, value]) => {
      let messages = compose(reject(isNil), map((rule) => (rule.validator(value) === false) ? rule.message : undefined))(rules[key])
      return messages.length > 0 ? [key + 'Errors', messages] : undefined
  }

  const errors = compose(fromPairs, reject(isNil), map(apply(rules)), toPairs, pick(keys(rules)))(data)

  if (Object.keys(errors).length === 0){
    return Observable.merge(Observable.of(reset(data)), Observable.of(success(data)))
  } else {
    return Observable.of(failure(data, errors))
  }
}

export default validator